<!-- Lynn -->
<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";

export default {
  name: "blacklist-component",
  components: { TableComponent, Pagination1 },
  data() {
    return {
      form: {
        blacklist_Id: "", // 身分證字號
        name: "", // 姓名
        blacklist_Note: "", // 原因
        create_Date: "",
        create_User: "",
        create_Name: "",
      },
      rules: {
        blacklist_Id: [
          { required: true, message: "必填", blur: "blur" },
          { len: 10, message: "身分證長度應為10碼", blur: "blur" },
        ],
        name: [{ required: true, message: "必填", blur: "blur" }],
      },

      blacklistTableColumns: [
        { id: "index", label: "項次", width: "75" },
        { id: "name", label: "姓名", width: "150" },
        { id: "blacklist_Id", label: "身份證字號", width: "200" },
        { id: "blacklist_Note", label: "原因", width: "" },
      ],
    };
  },
  computed: {
    // 黑名單資料 (All)
    blacklist() {
      return this.$store.state.blacklist;
    },
    // 黑名單資料 (單頁)
    blacklistList() {
      return this.$store.state.blacklist_list;
    },
  },
  methods: {
    addBlacklist(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          try {
            const res = await this.$store.dispatch("addBlacklist", this.form);
            if (res.data.isOK) {
              this.$message({
                type: "success",
                duration: 5000,
                showClose: true,
                message: "送出成功",
              });

              // 清空表單，並更新黑名單 (呈現第一頁)
              this.$refs[formName].resetFields();
              this.$store.commit("CURRENT_PAGE_INIT");
            }
          } catch (err) {
            console.log(err);
          }
        } else {
          this.$message({
            type: "error",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    async delBlacklist(data) {
      try {
        const res = await this.$store.dispatch("delBlacklist", data);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "修改成功",
          });

          this.$store.commit("CURRENT_PAGE_INIT");
        }
      } catch (err) {
        console.log(err);
      }
    },

    changePageHandler(page) {
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "blacklist",
        paramsListName: "blacklist_list",
      });
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得資料，呈現表格第一頁
    try {
      await this.$store.dispatch("getBlacklist");
    } catch (err) {
      console.log(err);
    }
    this.$store.commit("CURRENT_PAGE_INIT");
  },
};
</script>

<template src="./blacklist.html"></template>
